.wrapper__container {
  width: 100%;
  background-color: beige;
  paddig: 0;
  margin: 0; }

.wrapper__header {
  background-color: aqua;
  position: absolute;
  top: 0px;
  left: 0px;
  height: 200px;
  right: 0px;
  overflow: hidden; }

.wrapper__children {
  background-color: burlywood;
  position: absolute;
  top: 200px;
  bottom: 10px;
  left: 0px;
  right: 0px;
  overflow: auto; }



